import MessageModal from '@/components/messageModal.vue';
export default {
	components: {
		MessageModal
	},
	data() {
		return {
			userInfo:{},
			showCheckModal:false,
			showShopModal:false,
			checktitle:'资料审核种',
			shoptitle:'你已具备开店资格',
			hasNews:false
		}
	},
	onShow(){
		this.infoData()
		this.getNewsType()
	},
	methods: {
		infoData(){
			let self=this
			this.$acFrame.HttpService.ownerInfoMain().then(res=>{
				if(res.success){
					let userInfo = res.data
					userInfo.imgHeadPath=this.$acFrame.Util.setImgUrl(userInfo.imgHeadPath,userInfo.genderType);
					self.userInfo = userInfo
				}
			})
		},
		getNewsType(){
			let self =this
			this.$acFrame.HttpService.newsType().then(res=>{
				if(res.success){
					let newsVO = res.data
					let nums = 0
					for(let i =1;i<4;i++){
						nums+=newsVO[i]
					}
					if(nums>0){
						self.hasNews = true
					}else {
						self.hasNews = false
					}
				}
			})
		},
		linkTo(url){
			if(url=="mysettings"){
				url+='?passwdPaySet='+userInfo.passwdPaySet
			}
			if(url=="activityLevel"){
				url+='?type=3'
			}
			uni.navigateTo({
				url: url
			});
		},
		callShop(phone){
			uni.makePhoneCall({
				phoneNumber:phone
			})
		},
		showModal(name){
			this[name]=true
		},
		sureModal(name){
			this[name]=false
		},
		closeModal(){
			this.showCheckModal=false
			this.showShopModal=false
		},
		getUserInfo(e){
			let userinfo = e.detail.userInfo;
			let self =this
			let params = {
				imgHead:userinfo.avatarUrl,
				nickName:userinfo.nickName,
			}
			this.$acFrame.HttpService.bindwxInfo(params).then(res=>{
				if(res.success){
					self.$acFrame.Util.mytotal('信息绑定成功！')
				}
			})
		}
	}
}
